
import { mapState } from 'vuex'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'login-view',
components: {
XInput,
Group,
XButton,
XDialog,
XHeader
},
data () {
return {
form: {
phone: '', // 
password: '', // 
type: '1', // 
code: '', // 
imageCode: '', // 
resource: '' 
},
captcha: ''
}
},
computed: mapState({
accountToken: state => state.user.accountToken
}),
created: function () {
},
methods: {
goRegistView: function () {
this.$router.push({path: '/login'})
},
reloadCaptcha () {
if (!this.form.phone) {
this.$vux.toast.text('', 'middle')
return
}
this.captcha = 'http://platform-trade.dktai.cn/loginAndRegister/getImgCode?mobile=' + this.form.phone + '&t=' + new Date()
},
getSmsCode: function () {
if (!this.form.phone) {
this.$vux.toast.text('', 'middle')
return
}
this.$store.dispatch('getSmsCode', this.form).then(res => {
if (res.data.success === true && res.data.code === '1') {
this.$vux.toast.text(res.data.message, 'middle')
} else {
this.$vux.toast.text(res.data.message, 'middle')
}
})
},
regist: function () {
if (!this.form.phone) {
this.$vux.toast.text('', 'middle')
return
}
if (!this.form.code) {
this.$vux.toast.text('', 'middle')
return
}
if (!this.form.password) {
this.$vux.toast.text('', 'middle')
return
}
this.$store.dispatch('regist', this.form).then(res => {
if (res.data.success === true && res.data.code === '1') {
this.$vux.toast.text(res.data.message, 'middle')
} else {
this.$vux.toast.text(res.data.message, 'middle')
}
})
}
}
}



